<template>
  <CChartBar
    :datasets="computedDatasets"
    :options="computedOptions"
    :labels="computedLabels"
  />
</template>

<script>
import { CChartBar } from "@coreui/vue-chartjs";
import { getColor } from "@coreui/utils/src";

export default {
  name: "DeviceUsage",
  components: { CChartBar },
  props: {
    ...CChartBar.props,
    backgroundColorUpload: {
      type: String,
      default: "rgba(0,0,0,.2)",
    },
    backgroundColorDownload: {
      type: String,
      default: "rgba(255,0,0,.2)",
    },
    pointHoverBackgroundColor: String,
    dataUpload: {
      type: Array,
      default: () => [],
    },
    dataDownload: {
      type: Array,
      default: () => [],
    },
    labels: {
      type: Array,
      default: () => [],
    },
    labelUpload: {
      type: String,
      default: "Upload",
    },
    labelDownload: {
      type: String,
      default: "Download",
    },
    pointed: Boolean,
    ipDeviceUsageInstance: {
      type: Object,
      default: undefined,
    },
  },
  destroyed() {
    if (this.ipDeviceUsageInstance) {
      this.ipDeviceUsageInstance.offNewData(this.dataReadyHandler);
    }
  },
  methods: {
    dataReadyHandler: function onUsageDataReady(_sender, data) {
      this.dataUpload.splice(0, this.dataUpload.length);
      this.dataDownload.splice(0, this.dataDownload.length);
      this.labels.splice(0, this.labels.length);
      data.devicesUsage.forEach((d) => {
        this.dataUpload.push(d.srcBytes / 0x40000000);
        this.dataDownload.push(d.dstBytes / 0x40000000);
        // var label = d.name.length > 15 ?
        //             d.name.substring(0, 15 - 3) + "..." :
        //             d.name;
        this.labels.push(d.name);
      });
    },
  },
  watch: {
    ipDeviceUsageInstance: function() {
      if (this.ipDeviceUsageInstance) {
        this.ipDeviceUsageInstance.offNewData(this.dataReadyHandler);
        this.ipDeviceUsageInstance.onNewData(this.dataReadyHandler);
      }
    },
  },
  computed: {
    computedDatasets() {
      return [
        {
          data: this.dataUpload,
          backgroundColor: getColor(this.backgroundColorUpload),
          pointHoverBackgroundColor: getColor(this.pointHoverBackgroundColor),
          label: this.labelUpload,
          barPercentage: 0.5,
          categoryPercentage: 1,
        },
        {
          data: this.dataDownload,
          backgroundColor: getColor(this.backgroundColorDownload),
          pointHoverBackgroundColor: getColor(this.pointHoverBackgroundColor),
          label: this.labelDownload,
          barPercentage: 0.5,
          categoryPercentage: 1,
        },
      ];
    },
    computedLabels() {
      return this.labels;
    },
    computedOptions() {
      return {
        maintainAspectRatio: false,
        responsive: true,
        legend: {
          display: false,
        },
        scales: {
          xAxes: [
            {
              display: true,
              stacked: true,
            },
          ],
          yAxes: [
            {
              display: true,
              stacked: true,
            },
          ],
        },
      };
    },
  },
};
</script>

