



















import { ColorProgress } from "@/views/common";
import { IPLiveDataArgs } from "@/iphome/realtime/IPLive";
export default {
  name: "ResourceUsage",
  components: {
    ColorProgress,
  },
  props: {
    ipLiveInstance: {
      type: Object,
      default: undefined,
    },
  },
  data() {
    return {
      cpuValue: 0,
      tempValue: 0,
      memValue: 0,
      diskValue: 0,
      upSeconds: 0,
    };
  },

  methods: {
    dataReadyHandler: function(_sender, ipLiveInfoArgs: IPLiveDataArgs) {
      const sysInfo = ipLiveInfoArgs.cur._sysData;
      this.cpuValue = sysInfo.cpu;
      this.tempValue = sysInfo.temp;
      this.memValue = sysInfo.ram;
      this.diskValue = (sysInfo.disk.usd / sysInfo.disk.sz) * 100;
      this.upSeconds = ipLiveInfoArgs.cur._sysData.up;
    },
  },

  watch: {
    ipLiveInstance: function() {
      if (this.ipLiveInstance) {
        this.ipLiveInstance.offNewData(this.dataReadyHandler);
        this.ipLiveInstance.onNewData(this.dataReadyHandler);
      }
    },
  },
  computed: {
    uptime() {
      if (this.upSeconds > 0) {
        const days = ((this.upSeconds / 86400) | 0).toFixed(0);
        const hours = (
          "0" + (((this.upSeconds % 86400) / 3600) | 0).toFixed(0)
        ).slice(-2);
        const minutes = (
          "0" + (((this.upSeconds % 3600) / 60) | 0).toFixed(0)
        ).slice(-2);
        const seconds = ("0" + (this.upSeconds % 60)).slice(-2);
        return `${days} day${
          days === "1" ? "" : "s"
        }, ${hours}:${minutes}:${seconds}`;
      } else {
        return "";
      }
    },
  },
};
