/**
 * Submit and retrieve group info.
 * Subscribers got notified when data is successfully retrieved/submitted.
 */
import axios from "axios";
import { IEventHandler, LiteEvent } from "./common/LiteEvent";

export interface ipGroupInfo {
  mac: string;
  group_id: string;
  name: string;
  ex_name: string;
  ip: string;
}
//Usage data arguments
export interface IPGroupArgs {
  ipGroupInfo: Array<ipGroupInfo>;
}
export class IPGroup {
  private _usageDataRetrieved = new LiteEvent<IPGroup, IPGroupArgs>();

  constructor() {}

  /**
   * Retrieves subscription info
   */
  private _retrieveData(ip: number): void {
    let url = `http://192.168.0.1:5000/ip-grp?ip=${ip}`;
    console.log(url);
    axios.get(url).then(
      (response) => {
        if (response.status === 200) {
          this._processNewData(response.data);
        } else {
          console.error("Server respond with error:" + response);
        }
      },
      (err) => {
        console.error("Server failure:" + err);
      }
    );
  }
  /**
   * Retrieves subscription info
   */
  private _updateData(
    ip: string,
    groupId: number,
    exName: string,
    block: number
  ): void {
    let url = `http://192.168.0.1:5000/ip-grp?ip=${ip}&grp=${groupId}&ex=${exName}&blk=${block}`;
    console.log(url);
    axios.get(url).then(
      (response) => {
        if (response.status === 200) {
          this._processNewData(response.data);
        } else {
          console.error("Server respond with error:" + response);
        }
      },
      (err) => {
        console.error("Server failure:" + err);
      }
    );
  }
  /**
   *
   * @param newData
   */
  private _processNewData(subInfo: Array<ipGroupInfo>): boolean {
    this._usageDataRetrieved.trigger(this, {
      ipGroupInfo: subInfo,
    });
    return true;
  }

  public retrieveData(_if: number): void {
    //TODO: check simulation option
    this._retrieveData(_if);
  }

  public updateData(
    ip: string,
    groupId: number,
    exName: string,
    block: number
  ): void {
    //TODO: check simulation option
    this._updateData(ip, groupId, exName, block);
  }

  //Subscribe for new data event
  public onNewData(handler: IEventHandler<IPGroup, IPGroupArgs>) {
    this._usageDataRetrieved.on(handler);
    return this;
  }

  //Un-subscribe from new data event
  public offNewData(handler: IEventHandler<IPGroup, IPGroupArgs>) {
    this._usageDataRetrieved.off(handler);
    return this;
  }
}
