/**
 * Realtime traffic is retrieved and processed periodically.
 * Subscribers got notified when data is successfully retrieved and processed.
 */
import axios from "axios";
import { IEventHandler, LiteEvent } from "../common/LiteEvent";
import { IIPTimedData } from "./IIPLive";
import { IPLiveData } from "./IPLiveData";
import { IPLiveSimulator } from "./IPLiveSimulator";

export enum LiveStatus {
  LIVE_STATUS_UP,
  LIVE_STATUS_DOWN,
  LIVE_STATUS_INTERNAL_ERROR,

  LIVE_STATUS_NOT_INITIALIZED,
}
/**
 * Live data event arguments
 */
export interface IPLiveDataArgs {
  cur: IPLiveData;
  prev?: IPLiveData;
}

export class IPLive {
  //Current devices data
  private _curTimeData?: IPLiveData;

  //previous devices data
  private _prevTimedData?: IPLiveData;

  //data retrieval rate in milliseconds
  private _samplingRate: number = 1000;

  //data retrieval status
  private _aliveCounter: number = 1;

  //New data retrieved event
  private _newLiveTrafficData = new LiteEvent<IPLive, IPLiveDataArgs>();

  //Live data not available
  private _liveTrafficStatus = new LiteEvent<IPLive, LiveStatus>();

  /**
   *
   */
  private _status: LiveStatus = LiveStatus.LIVE_STATUS_NOT_INITIALIZED;

  private static _timerId: number;

  private _liveSimulator: IPLiveSimulator = new IPLiveSimulator();

  constructor() {}

  /**
   * Retrieves realtime data, then move current to previous and finally update current with the retrieved data
   */
  private _retrieveData(): void {
    let url = "http://192.168.0.1:5000/ip-realtime?ext=mrg,sys";
    axios.get(url).then(
      (response) => {
        if (response.status === 200) {
          this._processNewData(response.data);
        } else {
          console.error("Server respond with error:" + response);
          this._gotoState(LiveStatus.LIVE_STATUS_DOWN);
        }
      },
      (err) => {
        console.error("Server failure:" + err);
        this._gotoState(LiveStatus.LIVE_STATUS_DOWN);
      }
    );
  }

  /**
   *
   * @param newData
   */
  private _processNewData(newData: IIPTimedData): void {
    if (this._curTimeData) {
      if (!this._prevTimedData) {
        this._prevTimedData = new IPLiveData();
      }
      this._prevTimedData.copy(this._curTimeData);
    } else {
      this._curTimeData = new IPLiveData();
    }
    this._curTimeData.setData(newData, this._prevTimedData);

    this._newLiveTrafficData.trigger(this, {
      cur: this._curTimeData,
      prev: this._prevTimedData,
    });

    this._gotoState(LiveStatus.LIVE_STATUS_UP);
    this._aliveCounter++;
  }

  private _gotoState(newState: LiveStatus): void {
    if (this._status !== newState) {
      this._status = newState;
      this._liveTrafficStatus.trigger(this, newState);
    }
  }
  public start(simulation: boolean = false): void {
    if (IPLive._timerId) {
      window.clearInterval(IPLive._timerId);
    }
    IPLive._timerId = window.setInterval(() => {
      if (simulation) {
        this._processNewData(this._liveSimulator.retrieveMrg());
      } else {
        this._retrieveData();
      }
    }, this._samplingRate);
  }

  public stop(): void {
    if (IPLive._timerId) {
      window.clearInterval(IPLive._timerId);
    }
  }

  public samplingRage(): number {
    return this._samplingRate;
  }
  public aliveCounter(): number {
    return this._aliveCounter;
  }

  //Subscribe for new data event
  public onNewData(handler: IEventHandler<IPLive, IPLiveDataArgs>) {
    this._newLiveTrafficData.on(handler);
    return this;
  }

  //Un-subscribe from new data event
  public offNewData(handler: IEventHandler<IPLive, IPLiveDataArgs>) {
    this._newLiveTrafficData.off(handler);
    return this;
  }

  //Subscribe for new data event
  public onStatusChanged(handler: IEventHandler<IPLive, LiveStatus>) {
    this._liveTrafficStatus.on(handler);
    return this;
  }

  //Un-subscribe from new data event
  public offStatusChanged(handler: IEventHandler<IPLive, LiveStatus>) {
    this._liveTrafficStatus.off(handler);
    return this;
  }
}
