<template>
  <div>
    <CChartDoughnut
      :datasets="defaultDatasets"
      :options="defaultOptions"
      :labels="['Consumed', 'Remaining']"
    />
    <CInput :value="daysToRenew" horizontal plaintext />
    <CInput :value="consumed" horizontal plaintext />
    <CInput :value="remaining" horizontal plaintext />
  </div>
</template>

<script>
import { CChartDoughnut } from "@coreui/vue-chartjs";

export default {
  name: "CurrentMonth",
  components: { CChartDoughnut },
  props: {
    ipSubscriptionInstance: {
      type: Object,
      default: undefined,
    },
  },
  data() {
    return {
      pkg: 0,
      remDays: 0,
      remPackage: 0,
    };
  },
  mounted() {},
  destroyed() {
    this.ipSubscriptionInstance.offNewData(this.dataReadyHandler);
  },
  methods: {
    retrieveData: function() {
      if (this.netIfIndex > -1) {
        this.ipSubscription.retrieveData(this.netIfIndex);
      }
    },
    dataReadyHandler: function(_sender, ipSubInfoArgs) {
      const subInfo = ipSubInfoArgs.ipSubscriptionInfo;
      if (subInfo) {
        this.pkg = subInfo.package;
        this.remDays = subInfo.remDays;
        this.remPackage = subInfo.remPackage;
      }
    },
  },
  computed: {
    defaultDatasets() {
      return [
        {
          backgroundColor: ["rgba(255,0,0,.2)", "rgba(150, 54, 148,1)"],
          data: [
            (((this.pkg - this.remPackage) / this.pkg) * 100).toFixed(0),
            ((this.remPackage / this.pkg) * 100).toFixed(0),
          ],
        },
      ];
    },
    defaultOptions() {
      return {
        legend: {
          display: false,
        },
      };
    },
    remaining() {
      const remPkg = this.remPackage.toFixed(1);
      return `${remPkg} GB - (${(remPkg / this.remDays).toFixed(
        0
      )} GB/Day) Remaining`;
    },
    consumed() {
      const cons = (this.pkg - this.remPackage).toFixed(1);
      return `${cons} GB - (${(cons / (30 - this.remDays + 1)).toFixed(
        0
      )} GB/Day) Consumed`;
    },
    daysToRenew() {
      return `${this.remDays} Day${this.remDays !== 1 ? "s" : ""} to Renew`;
    },
  },

  watch: {
    ipSubscriptionInstance: function() {
      if (this.ipSubscriptionInstance) {
        this.ipSubscriptionInstance.offNewData(this.dataReadyHandler);
        this.ipSubscriptionInstance.onNewData(this.dataReadyHandler);
      }
    },
  },
};
</script>
