














































interface ITableDeivce {
  ip: string;
  mac: string;
  lease: string;
}
interface ITableTraffic {
  upload: string;
  download: string;
}
interface ITableConsumption {
  upload: string;
  download: string;
}
interface ITableItem {
  name: string;
  group: string;
  device: ITableDeivce;
  traffic: ITableTraffic;
  consumption: ITableConsumption;
}
let tableItems: Array<ITableItem> = [];

export default {
  name: "DeviceGroups",
  components: {},
  data() {
    return {
      tableItems: tableItems,
      tableFields: [
        { key: "device" },
        { key: "name" },
        { key: "group" },
        { key: "consumption" },
        { key: "traffic" },
      ],
    };
  },
  methods: {
    color(value: number) {
      let $color;
      if (value <= 25) {
        $color = "info";
      } else if (value > 25 && value <= 50) {
        $color = "success";
      } else if (value > 50 && value <= 75) {
        $color = "warning";
      } else if (value > 75 && value <= 100) {
        $color = "danger";
      }
      return $color;
    },
  },
};



