export class IPLiveSimulator {
  private _srcBytes1: number = 0;
  private _dstBytes1: number = 0;

  private _srcBytes2: number = 0;
  private _dstBytes2: number = 0;

  private _srcBytes3: number = 0;
  private _dstBytes3: number = 0;

  private _srcBytes4: number = 0;
  private _dstBytes4: number = 0;

  private _srcBytes5: number = 0;
  private _dstBytes5: number = 0;

  private static _random(min: number, max: number) {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }

  /**
   * url = "http://192.168.0.1:5000/ip-realtime?ext=mrg";
   */
  public retrieveMrg(): any {
    this._srcBytes1 += IPLiveSimulator._random(0, 0x8000);
    this._dstBytes1 += IPLiveSimulator._random(0, 0x400);

    this._srcBytes2 += IPLiveSimulator._random(0, 0x8000);
    this._dstBytes2 += IPLiveSimulator._random(0, 0x400);

    this._srcBytes3 += IPLiveSimulator._random(0, 0x8000);
    this._dstBytes3 += IPLiveSimulator._random(0, 0x400);

    this._srcBytes4 += IPLiveSimulator._random(0, 0x8000);
    this._dstBytes4 += IPLiveSimulator._random(0, 0x400);

    this._srcBytes5 += IPLiveSimulator._random(0, 0x8000);
    this._dstBytes5 += IPLiveSimulator._random(0, 0x400);

    const responseData = {
      mrg: [
        {
          name: "new-PC",
          ip: "192.168.0.128",
          mac: "00:11:22:33:44:55",
          src_bytes: this._srcBytes1,
          dst_bytes: this._dstBytes1,
        },
        {
          name: "iPad",
          ip: "192.168.0.170",
          mac: "aa:bb:cc:dd:ee:ff",
          src_bytes: this._srcBytes2.toString(),
          dst_bytes: this._dstBytes2.toString(),
        },
        {
          name: "PC128",
          ip: "192.168.0.199",
          mac: "a1:b1:c1:d1:e1:f1",
          src_bytes: this._srcBytes3.toString(),
          dst_bytes: this._dstBytes3.toString(),
        },
        {
          name: "kodi",
          ip: "192.168.0.184",
          mac: "b1:b2:b3:b4:b5:b6",
          src_bytes: this._srcBytes4.toString(),
          dst_bytes: this._dstBytes4.toString(),
        },
        {
          name: "LGwebOSTV",
          ip: "192.168.0.146",
          mac: "c1:c2:c3:c4:c5:c6",
          src_bytes: this._srcBytes5.toString(),
          dst_bytes: this._dstBytes5.toString(),
        },
      ],
      timestamp: Date.now() / 1000,
    };

    return responseData;
  }

  /**
   * http://192.168.0.1:5000/ip-usage?p=p&c=c
   */
  public retrieveUsage(p: string, c: number): any {
    const timestamp = Date.now() / 1000;
    const responseData = [
      {
        mac: "00:11:22:33:44:55",
        srcBytes: IPLiveSimulator._random(0, 0x80000) * c,
        dstBytes: IPLiveSimulator._random(0, 0x80000) * c,
        timestamp: timestamp,
        name: "new-PC",
        ip: "192.168.0.128",
      },
      {
        name: "iPad",
        ip: "192.168.0.170",
        mac: "aa:bb:cc:dd:ee:ff",
        timestamp: timestamp,
        srcBytes: IPLiveSimulator._random(0, 0x80000) * c,
        dstBytes: IPLiveSimulator._random(0, 0x80000) * c,
      },
      {
        name: "PC128",
        ip: "192.168.0.199",
        mac: "a1:b1:c1:d1:e1:f1",
        timestamp: timestamp,
        srcBytes: IPLiveSimulator._random(0, 0x80000) * c,
        dstBytes: IPLiveSimulator._random(0, 0x80000) * c,
      },
      {
        name: "kodi",
        ip: "192.168.0.184",
        mac: "b1:b2:b3:b4:b5:b6",
        timestamp: timestamp,
        srcBytes: IPLiveSimulator._random(0, 0x80000) * c,
        dstBytes: IPLiveSimulator._random(0, 0x80000) * c,
      },
      {
        name: "LGwebOSTV",
        ip: "192.168.0.146",
        mac: "c1:c2:c3:c4:c5:c6",
        timestamp: timestamp,
        srcBytes: IPLiveSimulator._random(0, 0x80000) * c,
        dstBytes: IPLiveSimulator._random(0, 0x80000) * c,
      },
    ];

    return responseData;
  }
}
