<template>
  <CChartBar
    :datasets="computedDatasets"
    :options="computedOptions"
    :labels="computedLabels"
  />
</template>

<script>
import { CChartBar } from "@coreui/vue-chartjs";
import { getColor } from "@coreui/utils/src";

export default {
  name: "TotalUsage",
  components: { CChartBar },
  props: {
    ...CChartBar.props,
    backgroundColorUpload: {
      type: String,
      default: "rgba(0,0,0,.2)",
    },
    backgroundColorDownload: {
      type: String,
      default: "rgba(255,0,0,.2)",
    },
    pointHoverBackgroundColor: String,
    dataUpload: {
      type: Array,
      default: () => [],
    },
    dataDownload: {
      type: Array,
      default: () => [],
    },
    labels: {
      type: Array,
      default: () => [],
    },
    labelUpload: {
      type: String,
      default: "Upload",
    },
    labelDownload: {
      type: String,
      default: "Download",
    },
    pointed: Boolean,
    ipRedDetailsInstance: Object,
  },
  destroyed() {
    if (this.ipRedDetailsInstance) {
      this.ipRedDetailsInstance.offNewData(this.dataReadyHandler);
    }
  },
  methods: {
    dataReadyHandler: function onUsageDataReady(_sender, data) {
      const today = new Date();
      const dataTimeLine = data.redDetails.reverse();
      this.dataUpload.splice(0, this.dataUpload.length);
      this.dataDownload.splice(0, this.dataDownload.length);
      this.labels.splice(0, this.labels.length);
      const monthNames = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
      var dayNames = [
        "Sunday",
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
      ];
      dataTimeLine.forEach((item) => {
        item.timestamp = item.timestamp * 1000;
        this.dataUpload.push(item.srcBytes / 0x40000000);
        this.dataDownload.push(item.dstBytes / 0x40000000);
        switch (data.unit) {
          case "h":
            this.labels.push(new Date(item.timestamp).getHours());
            break;
          case "d":
            var d = new Date(item.timestamp);
            if (
              today.getMonth() === d.getMonth() &&
              today.getDate() === d.getDate()
            ) {
              this.labels.push("Today");
            } else {
              this.labels.push(dayNames[d.getDay()]);
            }
            break;
          case "m":
            this.labels.push(monthNames[new Date(item.timestamp).getMonth()]);
            break;
        }
      });
    },
  },
  watch: {
    ipRedDetailsInstance: function() {
      if (this.ipRedDetailsInstance) {
        this.ipRedDetailsInstance.offNewData(this.dataReadyHandler);
        this.ipRedDetailsInstance.onNewData(this.dataReadyHandler);
      }
    },
  },
  computed: {
    computedDatasets() {
      return [
        {
          data: this.dataUpload,
          backgroundColor: getColor(this.backgroundColorUpload),
          pointHoverBackgroundColor: getColor(this.pointHoverBackgroundColor),
          label: this.labelUpload,
          barPercentage: 0.5,
          categoryPercentage: 1,
        },
        {
          data: this.dataDownload,
          backgroundColor: getColor(this.backgroundColorDownload),
          pointHoverBackgroundColor: getColor(this.pointHoverBackgroundColor),
          label: this.labelDownload,
          barPercentage: 0.5,
          categoryPercentage: 1,
        },
      ];
    },
    computedLabels() {
      return this.labels;
    },
    computedOptions() {
      return {
        maintainAspectRatio: false,
        responsive: true,
        legend: {
          display: false,
        },
        scales: {
          xAxes: [
            {
              display: true,
              stacked: true,
            },
          ],
          yAxes: [
            {
              display: true,
              stacked: true,
            },
          ],
        },
      };
    },
  },
};
</script>

