
















































































import { RateFormat, UsageFormat } from '@/iphome/common/TrafficFormat';
import { IPLive } from "@/iphome/realtime/IPLive";
import { IIPRealtimeNewDataArgs } from "@/iphome/realtime/IPRealtimeTraffic";
import { IPGroup } from "@/iphome/IPGroup";

interface ITableDevice {
  ip: string;
  mac: string;
  lease: string;
}
interface ITableTraffic {
  upload: string;
  download: string;
}
interface ITableConsumption {
  upload: string;
  download: string;
}
interface ITableItem {
  device: ITableDevice;
  block: boolean;
  host: string;
  name: string;
  group: number;
  consumption: ITableConsumption;
  traffic: ITableTraffic;
}
let tableItems: Array<ITableItem> = [];

export default {
  name: "DevicesDetails",
  components: {},
  props: {
    ipRealtimeTrafficInstance: {
      type: Object,
      default: undefined,
    },
  },
  data() {
    return {
      tableItems: tableItems,
      tableFields: [
        { key: "device" },
        { key: "host" },
        { key: "speed" },
        { key: "consumed" },
        { key: "group" },
        { key: "block" },
      ],
      groupsOptions: [
        { value: 0, label: "Default" },
        { value: 1, label: "Work" },
        { value: 2, label: "Family" },
        { value: 3, label: "Kids" },
        { value: 4, label: "Guest" },
        { value: 5, label: "TV" },
        { value: 6, label: "Limited" },
      ],
      ipGroup: {
        type: Object,
        default: undefined,
      },
      editModal: false,
      editHostName: "",
      editItem: undefined,
    };
  },
  mounted() {
    this.ipGroup = new IPGroup();
  },
  methods: {
    color(value: number) {
      let $color;
      if (value <= 25) {
        $color = "info";
      } else if (value > 25 && value <= 50) {
        $color = "success";
      } else if (value > 50 && value <= 75) {
        $color = "warning";
      } else if (value > 75 && value <= 100) {
        $color = "danger";
      }
      return $color;
    },
    dataReadyHandler: function onUsageDataReady(
      _sender: IPLive,
      realtimeTrafficArgs: IIPRealtimeNewDataArgs
    ) {
      if (realtimeTrafficArgs.trafficData.length > 0) {
        realtimeTrafficArgs.trafficData.forEach((d) => {
          const outBytes = d.device.getOutBytes();
          const totalBytes = d.device.getTotalBytes();

          const outRate = d.device.getOutRate();
          const totalRate = d.device.getInRate();

          let itemIndex = tableItems.findIndex((i) => {
            return i.device.ip === d.device.ip[0];
          });
          if (itemIndex > -1) {
            if (!d.device.isInactive()) {
              const item = tableItems[itemIndex];
              item.group = d.device.groupId;
              item.host = d.device.hName;
              item.name = d.device.givenName;
              item.block = d.device.block > 0 ? true : false;
              item.device.mac = d.device.mac;
              item.traffic.upload = this.formatRateData(outRate);
              item.traffic.download = this.formatRateData(totalRate);
              item.consumption.upload = this.formatUsageData(outBytes);
              item.consumption.download = this.formatUsageData(totalBytes);
            } else {
              tableItems.splice(itemIndex, 1);
            }
          } else {
            if (!d.device.isInactive()) {
              tableItems.push({
                device: { mac: d.device.mac, ip: d.device.ip[0], lease: "" },
                host: d.device.hName,
                name: d.device.givenName,
                group: d.device.groupId,
                block: d.device.block > 0 ? true : false,
                consumption: {
                  upload: this.formatUsageData(outBytes),
                  download: this.formatUsageData(totalBytes),
                },
                traffic: {
                  upload: this.formatRateData(outRate),
                  download: this.formatRateData(totalRate),
                },
              });
            }
          }
        });
      }
    },
    formatUsageData: function(data: UsageFormat): string {
      return data.val.toFixed(2) + " " + data.unit;
    },
    formatRateData: function(data: RateFormat): string {
      return (data.rate > 0 ? data.rate.toFixed(1) : 0) + " " + data.unit;
    },

    onItemChanged: function(item) {
      this.ipGroup.updateData(
        item.device.ip,
        item.group,
        item.name,
        item.block ? 1 : 0
      );
    },
    onNameChanged: function() {
      this.editModal = false;

      this.ipGroup.updateData(
        this.editItem.device.ip,
        this.editItem.group,
        this.editHostName,
        this.editItem.block ? 1 : 0
      );
    },
    renameHost(item) {
      this.editItem = item;
      this.editHostName = item.name;
      this.editModal = true;
    },
  },
  watch: {
    ipRealtimeTrafficInstance: function() {
      if (this.ipRealtimeTrafficInstance) {
        this.ipRealtimeTrafficInstance.offNewData(this.dataReadyHandler);
        this.ipRealtimeTrafficInstance.onNewData(this.dataReadyHandler);
      }
    },
  },
};
