<template>
  <CChartLine
    :datasets="defaultDatasets"
    :options="defaultOptions"
    :labels="defaultLabels"
  />
</template>

<script>
import { CChartLine } from "@coreui/vue-chartjs";
import { getStyle } from "@coreui/utils/src";

function random(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min);
}

export default {
  name: "IpTraffic",
  components: {
    CChartLine,
  },
  mounted() {
    // IPRealtimeTrafficFactory.getIPRealtimeTrafficInstance().onNewData(
    //   this.dataReadyHandler
    // );
  },
  destroyed() {
    // IPRealtimeTrafficFactory.getIPRealtimeTrafficInstance().offNewData(
    //   this.dataReadyHandler
    // );
  },
  methods: {
    dataReadyHandler: function onUsageDataReady(
      _sender,
      realtimeTrafficArgs
    ) {},
  },
  computed: {
    defaultLabels() {
      let labels = [];
      for (let i = 0; i < 50; i++) {
        labels.push(i + 1);
      }
      return labels;
    },
    defaultDatasets() {
      const brandSuccess = getStyle("success2") || "#4dbd74";
      const brandInfo = getStyle("info") || "#20a8d8";
      const brandDanger = getStyle("danger") || "#f86c6b";

      let elements = 50;
      const data1 = [];
      const data2 = [];
      const data3 = [];

      for (let i = 0; i <= elements; i++) {
        data1.push(random(50, 200));
        data2.push(random(80, 100));
        data3.push(65);
      }
      return [
        {
          label: "Mac-book",
          backgroundColor: "transparent",
          borderColor: brandInfo,
          pointHoverBackgroundColor: brandInfo,
          borderWidth: 2,
          data: data1,
        },
        {
          label: "ahmed's iPhone",
          backgroundColor: "transparent",
          borderColor: brandSuccess,
          pointHoverBackgroundColor: brandSuccess,
          borderWidth: 2,
          data: data2,
        },
      ];
    },
    defaultOptions() {
      return {
        maintainAspectRatio: false,
        legend: {
          display: true,
        },
        scales: {
          xAxes: [
            {
              gridLines: {
                drawOnChartArea: false,
              },
            },
          ],
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                maxTicksLimit: 5,
                stepSize: Math.ceil(250 / 5),
                max: 250,
              },
              gridLines: {
                display: true,
              },
            },
          ],
        },
        elements: {
          point: {
            radius: 0,
            hitRadius: 10,
            hoverRadius: 4,
            hoverBorderWidth: 3,
          },
        },
      };
    },
  },
};
</script>
