export interface UsageFormat {
  val: number;
  unit: string;
}
export interface RateFormat {
  rate: number;
  unit: string;
}
const USAGE_UNITS = ["B", "KB", "MB", "GB", "TB"];
const RATE_UNITS = ["Kbps", "Mbps", "Gbps"];

export function formatBytes(val: number): UsageFormat {
  let unit = 0;
  while (val > 0x400 && unit < USAGE_UNITS.length) {
    val = val / 0x400;
    unit++;
  }
  return { val: val, unit: USAGE_UNITS[unit] };
}
export function formatRate(val: number): RateFormat {
  let unit = 0;
  val = val / 0x400;
  while (val > 0x400 && unit < RATE_UNITS.length) {
    val = val / 0x400;
    unit++;
  }
  return { rate: val, unit: RATE_UNITS[unit] };
}
