/**
 * Realtime traffic
 */
import { IPLiveDataArgs, IPLive } from "./IPLive";
import { IPDeviceData } from "./IPDeviceData";
import { IEventHandler, LiteEvent } from "../common/LiteEvent";
import { IPLiveData } from "./IPLiveData";

export interface IIPRealtimeNewDataArgs {
  inRate: number;
  outRate: number;
  trafficData: Array<IPRealtimeTrafficData>;
}

/**
 * Device information data with in/out rates relevant to the configured sampling factor
 */
export class IPRealtimeTrafficData {
  private _snapshots: number = 1;
  constructor(
    //Device information
    public readonly device: IPDeviceData,
    //download rate
    private _inRate: number,
    //upload rate
    private _outRate: number
  ) {}

  addSnapshot(inRate: number, outRate: number): void {
    this._snapshots++;
    this._inRate += inRate;
    this._outRate += outRate;
  }

  inRate(): number {
    return this._inRate / this._snapshots;
  }
  outRate(): number {
    return this._outRate / this._snapshots;
  }
}
export class IPRealtimeTraffic {
  /**
   *
   *Active devices data
   */
  _trafficData: Array<IPRealtimeTrafficData> = [];

  /**
   *
   *Number of samples before releasing data
   */
  _samplingRateFactor: number;

  _prevTimedRealtimeTraffic?: IPLiveData;

  /**
   *
   *New data retrieved event
   */
  private _newRealtimeTrafficData = new LiteEvent<
    IPRealtimeTraffic,
    IIPRealtimeNewDataArgs
  >();

  /**
   *
   * @param _live live data provider
   * @param _samplesCount number of samples per event
   */
  constructor(private _live: IPLive, private _samplesCount: number) {
    /**
     *
     *subscribe for new data event
     */
    _live.onNewData((live, timedDataArgs) => {
      this._onNewData(live, timedDataArgs);
    });

    //Configure sampling rate
    this._samplingRateFactor = (1000 / _live.samplingRage()) * _samplesCount;
  }

  /**
   * New traffic data is available
   * @param live
   * @param timedData
   */
  private _onNewData(live: IPLive, timedDataArgs: IPLiveDataArgs): void {
    this._samplesCount++; //increment sampling count
    if (this._prevTimedRealtimeTraffic === undefined) {
      //use first sample as a previous snapshot
      if (timedDataArgs.prev) {
        this._prevTimedRealtimeTraffic = timedDataArgs.prev;
      } else {
        this._prevTimedRealtimeTraffic = timedDataArgs.cur;
      }
    }
    //increment traffic data by the current snapshot
    timedDataArgs.cur._devicesData.forEach((device) => {
      const deviceRtData = this._trafficData.find((traffic) => {
        return traffic.device.ip === device.ip;
      });
      if (deviceRtData === undefined) {
        //found new device in this snapshot
        this._trafficData.push(
          new IPRealtimeTrafficData(device, device.inRate, device.outRate)
        );
      } else {
        //device already exists
        deviceRtData.addSnapshot(device.inRate, device.outRate);
      }
    });

    if (this._samplesCount >= this._samplingRateFactor) {
      //ready to broadcast traffic details
      this._newRealtimeTrafficData.trigger(this, {
        outRate: 0, //TBD: calculate all out rate
        inRate: 0, //TBD: calculate all in rate
        trafficData: this._trafficData,
      });

      //----reset
      this._samplesCount = 0;
      this._prevTimedRealtimeTraffic = undefined;
    }
  }
  /**
   * Subscribe for new data event
   * @param handler
   */
  public onNewData(
    handler: IEventHandler<IPRealtimeTraffic, IIPRealtimeNewDataArgs>
  ) {
    this._newRealtimeTrafficData.on(handler);
    return this;
  }

  /**
   * Un-subscribe from new data event
   * @param handler
   */
  public offNewData(
    handler: IEventHandler<IPRealtimeTraffic, IIPRealtimeNewDataArgs>
  ) {
    this._newRealtimeTrafficData.off(handler);
    return this;
  }

  /**
   * Total download from all active devices
   */
  inBytes(): number {
    return 0;
  }
  /**
   * Total upload from all active devices
   */
  outBytes(): number {
    return 0;
  }
}
