/**
 * A snapshot of the devices data(active devices and realtime traffic) at specific time
 */
import { IIPSystemInfo, IIPTimedData } from "./IIPLive";
import { IPDeviceData } from "./IPDeviceData";
export class IPLiveData {
  //Devices data
  _devicesData: Array<IPDeviceData> = [];

  //sys data
  _sysData: IIPSystemInfo;
  //timestamp when this snapshot is created
  _timestamp: number = 0;

  //snapshot index
  _dataIndex: number = 0;

  //in rate
  _inRate: number = 0;

  //out rate
  _outRate: number = 0;

  //in bytes
  _inBytes: number = 0;

  //out bytes
  _outBytes: number = 0;

  constructor() {}
  /**
   *
   * @param timedData Data as it is received from the gateway
   */
  setData(timedData: IIPTimedData, prevRealtimeTraffic?: IPLiveData): void {
    this._timestamp =
      timedData.timestamp === undefined ? 0 : timedData.timestamp;

    this._inBytes = 0;
    this._outBytes = 0;
    this._inRate = 0;
    this._outRate = 0;
    if (timedData.mrg) {
      timedData.mrg.forEach((activeDevice) => {
        //check if device is active
        const alreadyActive = this._devicesData.find((d) => {
          return d.ip[0] === activeDevice.ip;
        });

        let prevDeviceData: IPDeviceData | undefined;
        if (alreadyActive) {
          if (prevRealtimeTraffic) {
            prevDeviceData = prevRealtimeTraffic._devicesData.find((v) => {
              return v.ip === alreadyActive.ip;
            });
          }
          //device already exists set new data
          alreadyActive.setData(
            timedData.timestamp || 0,
            activeDevice.mac,
            [activeDevice.ip],
            activeDevice.name,
            activeDevice.ex_name,
            activeDevice.group_id,
            activeDevice.block,
            Number.parseInt(activeDevice.src_bytes),
            Number.parseInt(activeDevice.dst_bytes),
            prevDeviceData
          );
          //total rates
          this._inRate += alreadyActive.inRate;
          this._outRate += alreadyActive.outRate;
        } else {
          //New device has just got active
          const newDevice = new IPDeviceData(
            timedData.timestamp || 0,
            activeDevice.mac,
            [activeDevice.ip],
            activeDevice.name,
            activeDevice.ex_name,
            activeDevice.group_id,
            activeDevice.block,
            Number.parseInt(activeDevice.src_bytes),
            Number.parseInt(activeDevice.dst_bytes),
            prevDeviceData
          );
          this._devicesData.push(newDevice);
        }
        this._inBytes += Number.parseInt(activeDevice.src_bytes);
        this._outBytes += Number.parseInt(activeDevice.dst_bytes);
      });
    }
    if (timedData.sys) {
      this._sysData = timedData.sys;
    }
    if (timedData.timestamp) {
      this._timestamp = timedData.timestamp;
    }
  }

  copy(from: IPLiveData): void {
    this._devicesData = [];
    from._devicesData.forEach((d) => {
      this._devicesData.push(
        new IPDeviceData(
          d.timestamp,
          d.mac,
          d.ip,
          d.hName,
          d.givenName,
          d.groupId,
          d.block,
          d.outBytes,
          d.inBytes,
          d.prevData
        )
      );
    });
    this._dataIndex = from._dataIndex;
    this._inRate = from._inRate;
    this._outRate = from._outRate;
    this._inBytes = from._inBytes;
    this._outBytes = from._outBytes;
  }
  /**
   *This snapshot index
   */
  dataIndex(): number {
    return this._dataIndex;
  }

  /**
   *
   * @param max
   */
  devicesData(max: number): Array<IPDeviceData> {
    //TBD: return top max devices if max > 0
    return this._devicesData;
  }
}
