









import { IPLiveDataArgs } from "@/iphome/realtime/IPLive";
import { formatRate } from "@/iphome/common/TrafficFormat";

export default {
  name: "TotalTraffic",
  components: {},
  props: {
    ipLiveInstance: {
      type: Object,
      default: undefined,
    },
    trafficType: {
      type: String,
      default: "download",
    },
  },
  data() {
    return {
      rate: 0,
      unit: "",
    };
  },
  methods: {
    dataReadyHandler: function(_sender, liveInfoArgs: IPLiveDataArgs) {
      if (this.trafficType === "download") {
        const formattedRate = formatRate(liveInfoArgs.cur._inRate);
        this.rate = formattedRate.rate.toFixed(0);
        this.unit = formattedRate.unit;
      } else if (this.trafficType === "upload") {
        const formattedRate = formatRate(liveInfoArgs.cur._outRate);
        this.rate = formattedRate.rate.toFixed(0);
        this.unit = formattedRate.unit;
      }
    },
  },

  watch: {
    ipLiveInstance: function() {
      if (this.ipLiveInstance) {
        this.ipLiveInstance.offNewData(this.dataReadyHandler);
        this.ipLiveInstance.onNewData(this.dataReadyHandler);
      }
    },
  },
  computed: {
    totalTraffic() {
      if (this.trafficType === "download") {
        return this.inRate;
      } else if (this.trafficType === "upload") {
        return this.outRate;
      } else if (this.trafficType === "all") {
        return this.inRate + this.outRate;
      }
      return "";
    },
  },
};
