/**
 * Realtime traffic is retrieved and processed periodically.
 * Subscribers got notified when data is successfully retrieved and processed.
 */
import axios from "axios";
import { IEventHandler, LiteEvent } from "./common/LiteEvent";

export interface IPDeviceUsageData {
  mac: string;
  srcBytes: number;
  dstBytes: number;
  lastSrcBytes: number;
  lastDstBytes: number;
  timestamp: number;
  name: string;
  ip: string;
}
//Usage data arguments
export interface IPDeviceUsageArgs {
  devicesUsage: Array<IPDeviceUsageData>;
}
export class IPDeviceUsage {
  private _usageDataRetrieved = new LiteEvent<
    IPDeviceUsage,
    IPDeviceUsageArgs
  >();

  constructor() {}

  /**
   * Retrieves realtime data, then move current to previous and finally update current with the retrieved data
   */
  private _retrieveData(p: string, c: number): void {
    let url = `http://192.168.0.1:5000/ip-usage?p=${p}&c=${c}`;
    console.log(url);
    axios.get(url).then(
      (response) => {
        if (response.status === 200) {
          this._processNewData(response.data);
        } else {
          console.error("Server respond with error:" + response);
        }
      },
      (err) => {
        console.error("Server failure:" + err);
      }
    );
  }

  /**
   *
   * @param newData
   */
  private _processNewData(usageData: Array<IPDeviceUsageData>): boolean {
    //TODO: normalize the numbers in one range (KB,MB or GB)
    this._usageDataRetrieved.trigger(this, {
      devicesUsage: usageData,
    });
    return true;
  }

  public retrieveUsageData(periodUnit: string, count: number): void {
    //TODO: check simulation option
    this._retrieveData(periodUnit, count);
  }

  //Subscribe for new data event
  public onNewData(handler: IEventHandler<IPDeviceUsage, IPDeviceUsageArgs>) {
    this._usageDataRetrieved.on(handler);
    return this;
  }

  //Un-subscribe from new data event
  public offNewData(handler: IEventHandler<IPDeviceUsage, IPDeviceUsageArgs>) {
    this._usageDataRetrieved.off(handler);
    return this;
  }
}
