/**
 * Device information
 */
import {
  formatBytes,
  formatRate,
  RateFormat,
  UsageFormat,
} from "../common/TrafficFormat";

export class IPDeviceData {
  inRate: number = 0;
  outRate: number = 0;
  constructor(
    //timestamp
    public timestamp: number,
    //device mac address
    public mac: string,
    //In case of one mac got several IPs, the one at index 0 should be the current assigned one
    public ip: Array<string>,
    //host name
    public hName: string,
    //given (configured name per mac)
    public givenName: string,
    //group willing GOD
    public groupId: number,
    //blocking flag
    public block: number,
    //uploaded bytes
    public outBytes: number,
    //downloaded bytes
    public inBytes: number,
    //previous device data
    public prevData?: IPDeviceData
  ) {
    this._calcRates();
  }

  setData(
    //timestamp
    timestamp: number,
    //device mac address
    mac: string,
    //In case of one mac got several IPs, the one at index 0 should be the current assigned one
    ip: Array<string>,
    //host name
    hName: string,
    //given (configured name per mac)
    givenName: string,
    //group willing GOD
    groupId: number,
    //blocking state
    block: number,
    //uploaded bytes
    outBytes: number,
    //downloaded bytes
    inBytes: number,
    //previous device data
    prevData?: IPDeviceData
  ): void {
    this.timestamp = timestamp;
    this.mac = mac;
    this.ip = ip;
    this.hName = hName;
    this.givenName = givenName;
    this.groupId = groupId;
    this.block = block;
    this.outBytes = outBytes;
    this.inBytes = inBytes;
    this.prevData = prevData;

    this._calcRates();
  }
  /**
   * Calculate in/out rates
   * @param prevData previous device data
   * @param deltaMs  time diff in milliseconds
   */
  private _calcRates() {
    if (this.prevData) {
      const delta = this.timestamp - this.prevData.timestamp;
      if (delta > 0) {
        this.inRate = ((this.inBytes - this.prevData.inBytes) * 8) / delta;
        this.outRate = ((this.outBytes - this.prevData.outBytes) * 8) / delta;
      }
    } else {
      this.inRate = 0;
      this.outRate = 0;
    }
  }

  getInBytes(): UsageFormat {
    return formatBytes(this.inBytes);
  }
  getOutBytes(): UsageFormat {
    return formatBytes(this.outBytes);
  }
  getTotalBytes(): UsageFormat {
    return formatBytes(this.outBytes + this.inBytes);
  }

  getInRate(): RateFormat {
    return formatRate(this.inRate);
  }

  getOutRate(): RateFormat {
    return formatRate(this.outRate);
  }
  getTotalRate(): RateFormat {
    return formatRate(this.outRate + this.inRate);
  }

  isInactive(): boolean {
    if (
      this.mac === "<incomplete>" &&
      this.inRate === 0 &&
      this.outRate === 0
    ) {
      return true;
    }
    return false;
  }
}
