<template>
  <div>
    <CRow>
      <CCol sm="12">
        <CSelect
          label="Package:"
          :options="subscriptionPackageOptions"
          :value.sync="subscriptionPackageSelected"
          @change="onUserInput($event)"
        />
      </CCol>
    </CRow>
    <CRow>
      <CCol sm="12">
        <CInput
          label="Renew on:"
          type="date"
          :value.sync="subscriptionRenewSelected"
          :min.sync="renewDateMin"
          :max.sync="renewDateMax"
          @input="onUserInputDate($event)"
        />
      </CCol>
    </CRow>
    <CRow>
      <CCol sm="12">
        <CSelect
          label="Subscription:"
          :options="subscriptionTypeOptions"
          :value.sync="subscriptionTypeSelected"
          @change="onUserInput($event)"
        >
        </CSelect>
      </CCol>
      <CCol sm="12">
        <CInput
          label="Adjust:"
          append=" MB"
          :value="adjust"
          @change="onUserInput($event)"
        />
      </CCol>
    </CRow>
  </div>
</template>

<script>
import * as dateUtil from "../../iphome/common/DateUtil";
const SUB_TYPE_30_DAYS = 0;
const SUB_TYPE_MONTH = 1;
export default {
  name: "Subscription",
  components: {},
  props: {
    netIfIndex: {
      type: Number,
      default: -1,
    },
    ipSubscriptionInstance: {
      type: Object,
      default: undefined,
    },
  },
  data() {
    return {
      //Subscription period/type
      subscriptionTypeSelected: SUB_TYPE_30_DAYS,
      subscriptionTypeOptions: [
        { value: SUB_TYPE_30_DAYS, label: "30-Days" },
        { value: SUB_TYPE_MONTH, label: "Calendar Month" },
      ],

      //Subscription Package
      subscriptionPackageSelected: 200,
      subscriptionPackageOptions: [
        { value: 140, label: "140" },
        { value: 200, label: "200" },
        { value: 250, label: "250" },
        { value: 400, label: "400" },
        { value: 600, label: "600" },
        { value: 1000, label: "1T" },
        { value: 0, label: "Custom..." },
      ],

      //Renew month-day
      renewMin: "2021-04-10",
      renewMax: "2022-04-10",
      subscriptionRenewSelected: "",

      remDays: 0,
      remPackage: 0,

      ipSubscription: {
        type: Object,
        default: undefined,
      },
    };
  },
  mounted() {},
  destroyed() {
    this.ipSubscriptionInstance.offNewData(this.dataReadyHandler);
  },

  methods: {
    updateData: function() {
      this.ipSubscriptionInstance.updateData(
        this.netIfIndex,
        this.subscriptionPackageSelected,
        this.subscriptionRenewSelected,
        this.subscriptionTypeSelected
      );
    },
    dataReadyHandler: function(_sender, ipSubInfoArgs) {
      const subInfo = ipSubInfoArgs.ipSubscriptionInfo;
      if (subInfo) {
        this.remDays = subInfo.remDays;
        this.remPackage = subInfo.remPackage;
        this.subscriptionTypeSelected = subInfo.subsDays;
        this.subscriptionRenewSelected = subInfo.nextRenew;
        this.subscriptionPackageSelected = subInfo.package;
      }
    },
    onUserInput: function() {
      this.updateData();
    },
    onUserInputDate: function(newValue) {
      this.subscriptionRenewSelected = newValue;
      this.updateData();
    },
  },
  computed: {
    adjust() {
      return "0";
    },
    renewDateMin() {
      return dateUtil.dateToYMD(new Date());
    },
    renewDateMax() {
      return dateUtil.dateToYMD(dateUtil.addMonths(new Date(), 2));
    },
  },

  watch: {
    ipSubscriptionInstance: function() {
      if (this.ipSubscriptionInstance) {
        this.ipSubscriptionInstance.offNewData(this.dataReadyHandler);
        this.ipSubscriptionInstance.onNewData(this.dataReadyHandler);
      }
    },
  },
};
</script>
