/**
 * Submit and retrieve subscription details.
 * Subscribers got notified when data is successfully retrieved and processed.
 */
import axios from "axios";
import { IEventHandler, LiteEvent } from "./common/LiteEvent";

export interface ipSubscriptionInfo {
  interface: number;
  nextRenew: string;
  package: number;
  subsDays: number;
  remDays: number;
  remPackage: number;
}
//Usage data arguments
export interface IPSubscriptionArgs {
  ipSubscriptionInfo: Array<ipSubscriptionInfo>;
}
export class IPSubscription {
  private _usageDataRetrieved = new LiteEvent<
    IPSubscription,
    IPSubscriptionArgs
  >();

  constructor() {}

  /**
   * Retrieves subscription info
   */
  private _retrieveData(_if: number): void {
    let url = `http://192.168.0.1:5000/ip-subs?if=${_if}`;
    console.log(url);
    axios.get(url).then(
      (response) => {
        if (response.status === 200) {
          this._processNewData(response.data);
        } else {
          console.error("Server respond with error:" + response);
        }
      },
      (err) => {
        console.error("Server failure:" + err);
      }
    );
  }
  /**
   * Retrieves subscription info
   */
  private _updateData(_if: number, pkg: number, renTimestamp: number, subType: number): void {
    let url = `http://192.168.0.1:5000/ip-usubs?_if=${_if}&ren=${renTimestamp}&pkg=${pkg}&sub=${subType}`;
    console.log(url);
    axios.get(url).then(
      (response) => {
        if (response.status === 200) {
          this._processNewData(response.data);
        } else {
          console.error("Server respond with error:" + response);
        }
      },
      (err) => {
        console.error("Server failure:" + err);
      }
    );
  }
  /**
   *
   * @param newData
   */
  private _processNewData(subInfo: Array<ipSubscriptionInfo>): boolean {
    this._usageDataRetrieved.trigger(this, {
      ipSubscriptionInfo: subInfo,
    });
    return true;
  }

  public retrieveData(_if: number): void {
    //TODO: check simulation option
    this._retrieveData(_if);
  }

  public updateData(
    _if: number,
    nextRenew: number,
    _package: number,
    subsDays: number
  ): void {
    //TODO: check simulation option
    this._updateData(_if, nextRenew, _package, subsDays);
  }

  //Subscribe for new data event
  public onNewData(handler: IEventHandler<IPSubscription, IPSubscriptionArgs>) {
    this._usageDataRetrieved.on(handler);
    return this;
  }

  //Un-subscribe from new data event
  public offNewData(
    handler: IEventHandler<IPSubscription, IPSubscriptionArgs>
  ) {
    this._usageDataRetrieved.off(handler);
    return this;
  }
}
