















export default {
  name: "ColorProgress",
  components: {},
  props: {
    max: {
      type: Number,
      default: 100,
    },
    value: {
      type: Number,
      default: 1,
    },
    label: {
      type: String,
      default: "",
    },
    showPercentage: {
      type: Boolean,
      default: true,
    },
    showValue: {
      type: Boolean,
      default: false,
    },
    warnValue: {
      type: Number,
      default: 75,
    },
    dangerValue: {
      type: Number,
      default: 90,
    },
  },
  computed: {
    color() {
      if (this.value >= this.dangerValue) {
        return "gradient-danger";
      } else if (this.value >= this.warnValue) {
        return "gradient-warning";
      } else {
        return "gradient-success";
      }
    },
  },
};
